/* ABOUT */
.about {
  /* color: #59D6FC; */
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 100%;
  margin: 0;
  padding: 0;
}

.about-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-width: fit-content;
  min-height: fit-content;
  margin: 0 1em;
}